import React, { Component } from 'react';
import{Col,Row,Container} from "react-bootstrap";

export default class Sales extends Component {
    render() {
        return (
            <div>
              <Container>
                  <br></br>
                  <h1>Shopping Cart</h1>
                  <br/>
                  <br/>
                  <Row>
                      <Col sm={4}>
                          <img src="data:image/jpeg;base64," alt="book"/>
                          <p>book name</p>
                          <p>description</p>
                          <p>price</p>
                      </Col>
                      <Col sm={4}>
                      </Col>
                      <Col sm={4}>
                          (X) Remove From Cart
                          <br/>
                          (V) View details
                      </Col>
                  </Row>
                  <hr/>
                  <Row>
                      <Col sm={4}>
                          <img src="data:image/jpeg;base64," alt="book"/>
                          <p>book name</p>
                          <p>description</p>
                          <p>price</p>
                      </Col>
                      <Col sm={4}>
                      </Col>
                      <Col sm={4}>
                          (X) Remove From Cart
                          <br/>
                          (V) View details
                      </Col>
                  </Row>
              </Container>
            </div>
        )
    }
}
